import { useState } from "react";
import ProjectCard from "../components/ProjectCard";
import { fundProjs } from "../projects/projectsList";
import "./Projects.css";
const Projects = () => {
  const [indexFundProjs, setIndexFundProjs] = useState(0);
  const [indexFrontProjs, setIndexFrontProjs] = useState(0);
  const [indexBackProjs, setIndexBackProjs] = useState(0);
  const nextProj = () => {
    indexFundProjs < fundProjs.length - 1 ? setIndexFundProjs(indexFundProjs + 1) : setIndexFundProjs(0);
  };

  const mappingProjectList = (projectList) => projectList.map(({ title, summary, description, imgSrc, site, repo }) => {
    return <ProjectCard
      title={ title }
      summary={ summary }
      description={ description }
      image={ imgSrc }
      site={ site }
      repo={ repo } />;
  });
  return (
    <section>
      <h1>Meus projetos</h1>
      <main>
        <section>
          <details>
            <summary>Fundamentos</summary>
            <div className="Projects-projects-container">
              <button className="Projects-previous-button" type="button" onClick={ nextProj }>{ '<<' }</button>
              <button className="Projects-next-button" type="button" onClick={ nextProj }>{ '>>' }</button>
              <div>
                { mappingProjectList(fundProjs)[indexFundProjs] }
              </div>
            </div>
          </details>
        </section>
      </main>
    </section>
  );
};

export default Projects;
